import React from 'react';
import {Link} from "react-router-dom";
import img from "../prva.jpg";

function Naslovna() {
  return (
    <div>
        <table border="1px solid black">
            <tr>
                <td>
                    <Link to="/povijest">Povijest nacionalnog Park</Link> 
                </td>
                <td>
                    <Link to="/hiperveza">Više o Nacionalnom Parku</Link>
                </td>
            </tr>
        </table>
      <img src={img}></img>
      </div>
  );
}

export default Naslovna;
