import React from 'react';
import {Link} from "react-router-dom";
import img from "../druga.jpg";
import "../App.css";

function Povijest() {
  const poz = "Evo tekst";
return (
  <div>
  <form>
    <label>
      Name: 
      <input type="text" name="name" />
    </label>
    <br/>
    <br/>
    <label>
      Surname: 
      <input type="text" name="lastname" />
    </label>
    <br/>
    <br/>
    <label>Gender: </label>
    <select>
      <option value="male">Male</option>
      <option value="female">Female</option>
      <option selected value="other">Undefined</option>
    </select>
    <br/>
    <br/>
    <br/>
    <input type="submit" value="Submit"/>
    </form>
    <br/>
    <br/>
      <Link className="mi" to="/">Naslovna</Link>
      <Nova/>
    </div>
  );
}

function Nova() {
  let novo={marka: "opel",boja: "crvena"};
  return (
  <div>
    <br/> 
    Marka auta koji vozi kroz plitvisko jezero: {novo.marka} <br/>
    Boja auta koji vozi kroz plitvisko jezero: {novo.boja} <br/>
  </div>
  );
}


export default Povijest;
