import React from 'react';
import {Link} from "react-router-dom";
import img from "../treca.png";
import "../App.css";

function Hiperveza() {
  return (
    <div>
      <Link className="mi" to="/">Povratak na Naslovnicu</Link> 
      <img src={img}></img>
      <a href="https://np-plitvicka-jezera.hr/">Sluzbena stranica</a>
      <br/>
      <br/>
      <Newem/>
    </div>
  );
}

function Newem() {
  let table={height: "120cm",width: "120cm",thickness: "5cm"};
  table.height = "220cm";
  return (
    <div>
      tables height: {table.height} <br/>
      table width: {table.width} <br/>
      table thickness: {table.thickness} <br/>
    </div>
  );
}

export default Hiperveza;
